<template>
  <view class="app-check-records">
      <view class="no-data" v-if="list.length == 0">
      <view class="no-date-item">
        <image src="https://htl001.oss-cn-hangzhou.aliyuncs.com/images/no-reports.png" mode="widthFix" />
        <view class="no-date-title">暂无体检报告</view>
        <u-button type="primary" style="width: 34vw">预约体检</u-button>
      </view>
    </view>
    <view class="app-check-records-list-item"  v-for="(item,index) in list" :key='index+"12345"' @click="gotoDetail(item)">
      <view class="app-check-records-list-item-header">
        <view class="name">{{vuex_user.realName|| '无'}}<view class="small">(本人)</view></view>
        <view class="date">{{item.testDt}}</view>
      </view>
      <view class="app-check-records-list-item-bodyer">
        <view class="info">{{item.testCompanyName}}</view>
        <view class="info">体检号: 2137145218</view>
      </view>

      <view class="app-check-records-list-item-footer"> 查看体检报告解读 </view>
    </view>

    <!-- <view class="app-check-records-list-item" @click="gotoDetail()">
      <view class="app-check-records-list-item-header">
        <view class="name">李易锋<view class="small">(本人)</view></view>
        <view class="date">2022年09月24日</view>
      </view>
      <view class="app-check-records-list-item-bodyer">
        <view class="info">厦门弘爱医院体检中心</view>
        <view class="info">体检号: 2137145218</view>
      </view>

      <view class="app-check-records-list-item-footer"> 查看体检报告解读 </view>
    </view> -->
  </view>
</template>

<script>
import { userTestReportDataList } from "@/api/health";
export default {
  data() {
    return {
      list:[]
    };
  },
  onLoad(){
    this.getList()
  },
  methods: {
    getList(){
      userTestReportDataList({}).then(res=>{
        if(res.code == '0' && res.data){
          this.list = res.data.records
        }
      })
    },
    gotoDetail(item) {
      uni.navigateTo({ url: `/pages/check-records/detail?id=${item.id}` });
    },
  },
};
</script>

<style lang="scss">
page {
  background: #fafafa;
}
.app-check-records {
  width: 100vw;

  .app-check-records-list-item {
    margin: 14px;
    background: #fff;
    padding: 14px 20px;
    border-radius: 14px;
    padding-bottom: 0px;
  }
  .app-check-records-list-item-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 8px;
    .name {
      font-size: 20px;
      font-weight: bold;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      .small {
        font-size: 12px;
        color: #999;
        font-weight: 400;
        margin-left: 4px;
      }
    }
    .date {
      color: #999;
      font-size: 12px;
    }
  }
  .app-check-records-list-item-bodyer {
    padding-bottom: 14px;
    border-bottom: 1px #eee solid;
    .info {
      font-size: 13px;
      color: #666;
      line-height: 1.6;
    }
  }
  .app-check-records-list-item-footer {
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: #3873fe;
  }
}

.no-data {
  width: 100vw;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  .no-date-item {
    width: 80vw;
    .no-date-title{
      text-align: center;
      font-size: 20px;
      margin-bottom: 14px;
      font-weight: bold;
    }
  }
  
}
</style>